import api from "./../../api";
// initial state
// shape: [{ id, quantity }]
const state = {
  cartList: []
};

// getters
const getters = {
  // 
  cartProducts: state => {
    return state.cartList.map(item => {
      const price = item.$pet.price;
      return {
        price: price || 1,
        quantity: item.count
      };
    });
  },
  // 
  cartTotalPrice: (state, getters) => {
    return getters.cartProducts.reduce((total, product) => {
      return total + product.price * product.quantity;
    }, 0);
  }
};

// actions
const actions = {
  // 
  getCartList({ commit }, { user_id = "" }) {
    if (!user_id) {
      return;
    }
    api("cart/read", {
      where: {
        user_id: user_id
      },
      with: "has_one:pet"
    }).then(r => {
      console.log(r);
      let result = r.data || [];
      commit("setCartItems", result);
    });
  },
  //  cart 
  removeCartItem({ commit }, id) {
    api("cart/delete", {
      id: id
    }).then(r => {
      console.log(r.success);
      if (r.success) {
        commit("removeProductFromCart", id);
      }
    });
  },
  //  item  cart 
  addItemToCart({ commit }, obj) {
    api("cart/create", obj)
      .then(r => {
        if (r.success) {
          return r.data.user_id;
        } else {
          return false;
        }
      })
      .then(user_id => {
        if (user_id === false) {
          return;
        }
        api("cart/read", {
          where: {
            user_id: user_id
          },
          with: "has_one:pet"
        }).then(r => {
          let result = r.data;
          if (r.data == null) {
            result = [];
          }
          commit("setCartItems", result);
        });
      });
  }
};

// mutations
const mutations = {
  // 
  pushProductToCart(state, obj) {
    state.cartList.push(obj);
  },
  // 
  removeProductFromCart(state, id) {
    let result = state.cartList.filter(item => {
      return item.id !== id;
    });
    state.cartList = result;
  },

  incrementItemQuantity(state, { id }) {
    const cartItem = state.items.find(item => item.id === id);
    cartItem.quantity++;
  },
  // 
  setCartItems(state, list) {
    state.cartList = list;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
