<template>
  <div>
    <div class="level">
      <div class="level-left">
        <Input v-model="keyword" @on-enter="handleSearch" placeholder="">
        <Button slot="append" icon="ios-search"></Button>
        </Input>
      </div>
      <div class="level-right">
        <Button type="primary" @click="showForm = true"></Button>
      </div>
    </div>

    <section class="box" v-if="showForm">
      <Form :model="current" @submit.native.prevent="handleSubmit">
        <h2></h2>
        <FormItem label="" prop="title">
          <Input v-model="current.title" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="color">
          <Input v-model="current.color" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="description">
          <Input v-model="current.description" type="textarea" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="price">
          <Input v-model="current.price" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="character">
          <Input v-model="current.character" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="birthday">
          <Input v-model="current.birthday" type="date" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="cover_url">
          <Input v-model="current.cover_url" placeholder=""></Input>
        </FormItem>
        <FormItem label="" prop="breed_id">
          <Select v-model="current.breed_id" filterable :remote-method="getBreedList" :loading="loading" placeholder="">
            <Option v-for="(option, index) in breedOptions" :value="option.value" :key="index">{{option.label}}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="category_id">
          <Select v-model="current.category_id" filterable :remote-method="getCategoryList" :loading="loading" placeholder="">
            <Option v-for="(option, index) in categoryOptions" :value="option.value" :key="index">{{option.label}}</Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="neuter">
          <RadioGroup v-model="current.neuter">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
            {{current.neuter}}
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="vaccinated">
          <RadioGroup v-model="current.vaccinated">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="pure_breed">
          <RadioGroup v-model="current.pure_breed">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="disease">
          <RadioGroup v-model="current.disease">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="" prop="sex">
          <RadioGroup v-model="current.sex">
            <Radio label="1"></Radio>
            <Radio label="0"></Radio>
          </RadioGroup>
        </FormItem>
        <Button type="primary" html-type="submit"></Button>
        <Button @click="showForm = false" style="marginLeft:10px"></Button>
      </Form>
    </section>

    <Table :columns="columnsConfig" :data="list"></Table>
  </div>
</template>

<script>
// import api from "./../../api/";

import AdminMixinsVue from "./mixins/AdminMixins.vue";
import api from "./../../api";
export default {
  mixins: [AdminMixinsVue],
  data() {
    return {
      model: "pet",
      current: {},
      currentPage: 1,
      loading: false,
      categoryOptions: [],
      searchable: ["title"],
      breedOptions: [],
      with: { relation: "has_one", model: "breed" },
      columnsConfig: [
        {
          title: "",
          key: "title",
          fixed: "left",
          width: 140
        },
        {
          title: "",
          key: "price",
          width: 80
        },
        {
          title: "",
          key: "category_id",
          width: 80
        },
        {
          title: "",
          key: "$breed",
          width: 100
        },
        {
          title: "",
          key: "character",
          width: 100
        },
        {
          title: "",
          key: "neuter",
          width: 100
        },
        {
          title: "",
          key: "pure_breed",
          width: 100
        },
        {
          title: "",
          key: "vaccinated"
        },
        {
          title: "",
          key: "action",
          width: 180,
          fixed: "right",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary"
                    // size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.handleEditItem(params);
                    }
                  }
                },
                "Edit"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "warning"
                    // size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.handleRemoveItem(params.row.id);
                    }
                  }
                },
                "Delete"
              )
            ]);
          }
        }
      ]
    };
  },
  methods: {
    //  category
    getCategoryList(query) {
      this.loading = true;

      clearTimeout(this.timer);

      this.timer = setTimeout(() => {
        api(`category/search`, { or: { name: query } }).then(r => {
          this.loading = false;
          if (r.data === null) {
            return;
          }
          this.categoryOptions = r.data.map(item => {
            return {
              value: item.id,
              label: item.name
            };
          });
        });
      }, 300);
    },
    getBreedList(query) {
      this.loading = true;

      clearTimeout(this.timer);

      this.timer = setTimeout(() => {
        api(`breed/search`, { or: { name: query } }).then(r => {
          this.loading = false;
          if (r.data == null || r.data.length == 0) {
            return;
          }
          this.breedOptions = r.data.map(item => {
            return {
              value: item.id,
              label: item.name
            };
          });
        });
      }, 300);
    }
    // handleSearch() {

    // }
  }
};
</script>

