
<script>
import api from "./../../../api";
export default {
  data() {
    return {
      showForm: false, // 
      list: [], //  list
      keyword: "", // 
      searchable: ["name"], // 
      current: {}, // 
      currentPage: 1, // 
      timer: 0, //  debouce 
      with: {}, //  read 
      // 
      columnsConfig: [
        {
          title: "",
          key: "name"
        },
        {
          title: "",
          key: "action",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary"
                    // size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.handleEditItem(params);
                    }
                  }
                },
                "Edit"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "warning"
                    // size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.handleRemoveItem(params.row.id);
                    }
                  }
                },
                "Delete"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.read();
  },
  methods: {
    /**
     * 
     */
    handleSubmit() {
      let action = this.current.id ? "update" : "create";
      console.log(this.current);
      api(`${this.model}/${action}`, this.current).then(() => {
        this.current = {};
        this.read();
        this.showForm = false;
      });
    },
    /**
     * 
     */
    handleEditItem(params) {
      this.showForm = true;
      this.current = Object.assign({}, params.row);
      for (let key in this.current) {
        let item = this.current[key];
        if (item === true) {
          this.current[key] = "1";
        } else if (item === false) {
          this.current[key] = "0";
        }
      }
    },
    /**
     * 
     */
    handleRemoveItem(id) {
      if (id < 0 || !id) {
        return;
      }
      if (!confirm("")) return;

      api(`${this.model}/delete`, { id }).then(() => {
        console.log("this.currentPage:", this.currentPage);
        this.read(this.currentPage);
      });
    },
    /**
     *  search 
     */
    handleSearch() {
      let params = {
        or: {}
      };
      this.searchable.forEach(item => {
        params.or[item] = this.keyword;
      });
      console.log(params);
      api(`${this.model}/search`, {
        or: params.or
      }).then(r => {
        if (r.data !== null) {
          this.list = r.data;
        } else {
          this.list = [];
        }
      });
    },
    /**
     *  model 
     */
    read() {
      api(`${this.model}/read`, {
        with: this.with
      }).then(r => {
        this.list = r.data;
      });
    }
  }
};
</script>
