import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import Search from "./views/Search.vue";
import Detail from "./views/Detail.vue";

//  
import AdminBase from "./views/admin/AdminBase.vue";
import Category from "./views/admin/Category.vue";
import UserManager from "./views/admin/UserManager.vue";
import Order from "./views/admin/Order.vue";
import Pet from "./views/admin/Pet.vue";
import Breed from "./views/admin/Breed.vue";

// 
import OrderBase from "./views/order/OrderBase.vue";
import NewOrder from "./views/order/NewOrder.vue";
import PayOrder from "./views/order/PayOrder.vue";

// 
import Login from "./views/Login.vue";
import Signup from "./views/Signup.vue";

// 
import Me from "./views/me/MeBase";
import MeOrder from "./views/me/Order";
import MeSetting from "./views/me/Setting";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
      meta: {
        title: ""
      }
    },
    {
      path: "/about",
      name: "about",
      component: About,
      meta: {
        title: ""
      }
    },
    {
      path: "/detail/:id",
      name: "detail",
      component: Detail,
      meta: {
        title: ""
      }
    },
    {
      path: "/search",
      name: "search",
      component: Search,
      meta: {
        title: ""
      }
    },
    {
      path: "/login",
      name: "login",
      component: Login,
      meta: {
        title: ""
      }
    },
    {
      path: "/signup",
      name: "signup",
      component: Signup,
      meta: {
        title: ""
      }
    },
    // order
    {
      path: "/order",
      name: "order",
      component: OrderBase,
      meta: {
        title: ""
      },
      children: [
        {
          path: "new",
          component: NewOrder,
          meta: {
            title: "-"
          }
        },
        {
          path: "pay/:oid",
          component: PayOrder,
          meta: {
            title: "-"
          }
        }
      ]
    },
    // admin
    {
      path: "/admin",
      name: "admin",
      component: AdminBase,
      meta: {
        title: ""
      },
      children: [
        {
          path: "category",
          component: Category,
          meta: {
            title: "-"
          }
        },
        {
          path: "user",
          component: UserManager,
          meta: {
            title: "-"
          }
        },
        {
          path: "order",
          component: Order,
          meta: {
            title: "-"
          }
        },
        {
          path: "pet",
          component: Pet,
          meta: {
            title: "-"
          }
        },
        {
          path: "breed",
          component: Breed,
          meta: {
            title: "-"
          }
        }
      ]
    },
    // me
    {
      path: "/me",
      name: "me",
      component: Me,
      meta: {
        title: ""
      },
      children: [
        {
          path: "order",
          component: MeOrder
        },
        {
          path: "setting",
          component: MeSetting
        }
      ]
    }
  ]
});
